* 
  box-sizing: border-box
html 
  line-height: 1.15 
  -ms-text-size-adjust: 100% 
  -webkit-text-size-adjust: 100% 
h1 
  font-size: 2em
  margin: 0.67em 0
dl dl,
dl ol,
dl ul,
ol dl,
ul dl 
  margin: 0
ol ol,
ol ul,
ul ol,
ul ul 
  margin: 0
hr 
  box-sizing: content-box 
  color: inherit 
  height: 0 
  overflow: visible 
main 
  display: block
pre 
  font-family: monospace, monospace 
  font-size: 1em 
  text-decoration: underline dotted
b,
strong 
  font-weight: bolder
code,
kbd,
samp 
  font-family: monospace, monospace 
  font-size: 1em 
small 
  font-size: 80%
audio,
video 
  display: inline-block
audio:not([controls]) 
  display: none
  height: 0
img 
  border-style: none
svg:not(:root) 
  overflow: hidden
table 
  border-color: inherit 
  text-indent: 0 
button,
input,
select 
  margin: 0
button 
  overflow: visible 
  text-transform: none 
button,
[type="button"],
[type="reset"],
[type="submit"] 
  -webkit-appearance: button
fieldset 
  padding: 0.35em 0.75em 0.625em
input 
  overflow: visible
legend 
  box-sizing: border-box 
  color: inherit 
  display: table 
  max-width: 100% 
  white-space: normal 
progress 
  display: inline-block 
  vertical-align: baseline 
select 
  text-transform: none
textarea 
  margin: 0 
  overflow: auto 
[type="checkbox"],
[type="radio"] 
  box-sizing: border-box 
  padding: 0 
[type="search"] 
  -webkit-appearance: textfield 
  outline-offset: -2px 
::-webkit-inner-spin-button,
::-webkit-outer-spin-button 
  height: auto
::-webkit-input-placeholder 
  color: inherit
  opacity: 0.54
::-webkit-search-decoration 
  -webkit-appearance: none
::-webkit-file-upload-button 
  -webkit-appearance: button 
  font: inherit 
::-moz-focus-inner 
  border-style: none
  padding: 0
:-moz-focusring 
  outline: 1px dotted ButtonText
:-moz-ui-invalid 
  box-shadow: none
details 
  display: block
dialog 
  background-color: white
  border: solid
  color: black
  display: block
  height: -moz-fit-content
  height: -webkit-fit-content
  height: fit-content
  left: 0
  margin: auto
  padding: 1em
  position: absolute
  right: 0
  width: -moz-fit-content
  width: -webkit-fit-content
  width: fit-content
dialog:not([open]) 
  display: none
summary 
  display: list-item
canvas 
  display: inline-block
template 
  display: none
[hidden] 
  display: none
